class Animal{
    constructor(options){
        this.name = options.name
        this.age = options.age
        this.hasTail = options.hasTail
    }

    voice(){
        console.log("I am PAnda")
    }
}


const animal = new Animal({
    name:"Panda",
    age:5,
    hasTail:true
});

console.log(animal.voice())
console.log(animal)